.about {
  display: flex;
  flex-direction: column; /* Change to column */
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url('../resources/earth.jpg');
  background-size: cover;
  background-position: center;
  color: white;
}

.containerOne {
  display: flex;
  flex-direction: column; /* Change to row */
  align-items: center;
  height: auto; /* Change to auto */
  margin-bottom: 20px; /* Add margin bottom to separate containers */
  transition: opacity 0.8s ease-in-out;
}

.hi {
  opacity:70%;
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  display: 'inline-block';
  margin: 0;
  text-shadow: 1px 1px 2px #000000, 0 0 0 #000000, 1px 1px 2px #000000; /* 3D effect */
}

.sreeKrishna {
  opacity:90%;
  font-family: 'Poppins', sans-serif;
  color: white;
  font-size: 60px;
  font-weight: 600;
  
}

.suresh {
  opacity:60%;
  font-family: 'Poppins', sans-serif;
  color: white;
  opacity:'85%';
  font-size: 60px;
  font-weight: 700;
}

.roles {
  opacity: 80%;
  color: white;
  font-Size: 40px;
  font-Weight: 600;
  text-shadow: 1px 1px 2px #000000, 0 0 0 #000000, 1px 1px 2px #000000; /* 3D effect */
}

.resume a{
  color: white; /* Adjust color as desired */
  text-decoration: none; /* Remove default underline */
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  opacity: 80%;
  font-size: 12px;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 10px rgba(255, 255, 255, 0.8); /* Shadow properties */
  border-radius: 15px; /* Rounded corners */
  padding: 5px;

}

.resume a:hover{
  background-color: white; /* White background */
  color: black;  /* Black text */
  border-radius: 20px; /* Add rounded corners */
  padding: 5px 10px;  /* Add some padding for visual balance */
  transition: all 0.2s ease-in-out; 
  font-size: 12px;
  font-weight: bold;
  opacity: 70%;
}

/* Style the paragraph text */
.noDisplay {
font-size: 14px;
margin-bottom: 15px;
font-family: 'Poppins', sans-serif;
opacity: 0; /* Initial opacity set to 0 for invisible state */
transition: opacity 0.8s ease-in-out; /* Adjust transition properties as needed */
}


/* Style the social links container */
.socialLinks {
display: flex;
justify-content: center;
align-items: center;
margin-top: 25px;
transition: opacity 0.8s ease-in-out;
padding-left: 15px;
}

/* Style the social media icons */
.socialLinks a {
margin-right: 10px;
color: white;
font-size: 20px;
transition: transform 0.2s ease; /* Adjust transition as needed */
}

.socialLinks a:hover {
font-size: 40px;
transition: font-size 0.2s ease;
transform: translateY(-5px); /* Adjust vertical movement for pop-up effect */
box-shadow: 0px 2px 5px rgb(255, 255, 255); /* Subtle shadow for depth */
}


